import * as THREE from 'three'

const SceneParser = (app) => {
    
    const Parse = () => {
        app.sceneGroup.traverse(function (child) {
            if (child instanceof THREE.Mesh && child.name.indexOf("reflective") >= 0) {
                child.material = new THREE.MeshStandardMaterial({
                    color: "#FFF",
                    roughness: 0.0,
                    metalness: 1.0,
                    envMapIntensity: 1.0,
                    envMap: app.envmap
                });
                child.material.needsUpdate = true
            }
        });
    }
    
    return { Parse }
}

export default SceneParser
