import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js'
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';

const scenes = [
    { scene: 'scene-1-004.fbx', envmap: null, skybox: 'test-skybox.hdr' },
    { scene: 'scene-1-004.fbx', envmap: null, skybox: null },
    { scene: 'scene-3-002.fbx', envmap: 'scene3-env-001.png', skybox: 'test-skybox.hdr' },
    { scene: 'fbx-test.fbx', envmap: 'scene3-env-001.png', skybox: 'test-skybox.hdr' },
];

const SceneLoader = (app) => {
    
    const LoadScene = (index, scale) => {
        if (scenes[index].scene == null) return;
        
        app.screens.blocker.style.display = 'none'
        app.screens.loader.style.display = 'block'
        
        const loadingManager = new THREE.LoadingManager(() =>
        {
            app.parser.Parse();
                        
            app.screens.loader.style.display = 'none'
            app.screens.blocker.style.display = 'block'
        },
        () =>
        {
            console.log('loading progressing')
        },
        () =>
        {
            console.log('loading error')
        })
        
        const fbxLoader = new FBXLoader(loadingManager)
        const textureLoader = new THREE.TextureLoader(loadingManager)
        const rgbeLoader = new RGBELoader(loadingManager);
        
        fbxLoader.load(
        `./data/${scenes[index].scene}`,
        (fbx) =>
        {
            console.log('success')

            app.controls.RemoveObjects(app.sceneGroup.getObjectByName( "Floor" ))
            while(app.sceneGroup.children.length > 0) {
                app.sceneGroup.remove(app.sceneGroup.children[0])
            }

            app.sceneGroup.add(fbx)
            fbx.scale.set(0.01, 0.01, 0.01)
            
            app.controls.AddObject(app.sceneGroup.getObjectByName( "Floor" ))
            
            if (fbx.animations != null) {
                console.log(fbx.animations)
                app.mixer = new THREE.AnimationMixer( fbx );
                const action = app.mixer.clipAction( fbx.animations[ 0 ] );
                action.play();
            }
        },
        (progress) =>
        {
        },
        (error) =>
        {
            console.log('error')
            console.log(error)
        })     
        
        if (scenes[index].skybox != null) {
            const hdrCubeMap = rgbeLoader
            .setDataType( THREE.UnsignedByteType ) 
            .load( './textures/skybox/' +scenes[index].skybox, function () {
                app.background = new THREE.WebGLCubeRenderTarget(hdrCubeMap.image.height);
                app.background.fromEquirectangularTexture(app.renderer, hdrCubeMap);
                app.scene.background = app.background;
            });
        } else {
            app.scene.background.dispose()
        }
        
        if (scenes[index].envmap != null) {
            const envmap = textureLoader.load(
            './textures/envmap/' +scenes[index].envmap,
            () => { 
              app.envmap = new THREE.WebGLCubeRenderTarget(envmap.image.height);
              app.envmap.fromEquirectangularTexture(app.renderer, envmap);
            });
        } else {
            app.envmap.dispose()
        }
        
    }

    return { LoadScene }
}

export default SceneLoader
